.image{
    width: 240px;
}

.search>form>input[type="search"]{
    text-transform: uppercase;
    width: 94%;
	border-radius:25px;
	font-size: 20px;
	outline: none;
	border: 1px solid black;
	margin:10px 0px;
	padding:5px 0px 5px 25px;
	box-sizing: border-box;;

    @import #submit{
		font-size: 18px;
		display: block;
		margin: 0 auto;
		background-color: white;
		color:black;
		outline: none;
		padding: 10px;
		border-radius: 30px;
        border:2px solid black;
    }
}
 

.search>form>input[type="submit"]{
	font-size: 18px; 
	padding: 4px; 
	border-radius: 30px;
}

#submit:hover {
	color:white;
	background-color: #282828;
	border:2px solid #282828;
	cursor:pointer;
}


.responseDiv {
	margin: 0px auto;
	box-sizing: border-box;
	width: 97%;
	padding: 25px 50px;
	box-sizing: border-box;
	margin:20px;
	position: realtive;
	left:50%;
	border: 4px solid blueviolet;
	font-family: 'Lucida Sans', 'Lucida Sans Regular', 'Lucida Grande', 'Lucida Sans Unicode', Geneva, Verdana, sans-serif;
	text-align: center;
	border-radius: 10%;
}
.responseDiv:hover{
	background-color: cyan;
}

.city{
	font-size:	2.5em;
}

.span1, .span2{
	font-size: 2em;
	margin: 10px;
}

.diffrence{
	display: inline-block;
	width: 50%;
	font-style: oblique;
}


.outer-box{
    display: inline-block;
    font-family: 'Lucida Sans', 'Lucida Sans Regular', 'Lucida Grande', 'Lucida Sans Unicode', Geneva, Verdana, sans-serif;
}

.box{
	margin: 30px auto;
	margin-left: 50px;
    padding: 10px 5px;
	border: 4px solid blueviolet;
	border-radius: 25%;
    width:  416px;
    height: 700px;
    text-align: center;
    display: inline-block;
    
    
}

.box:hover{
	background-color: khaki;
}

.outerbox2{
	margin-left: 250px;
}

.box1{
	margin: 30px auto;
	margin-left: 50px;
    padding: 10px 5px;
	border: 4px solid blueviolet;
	border-radius: 25%;
    width:  380px;
